<template>
  <div class="signin-container">
    <el-card class="box-card">
      <el-form ref="form" :model="form" class="form">
        <h1>{{ $t("top.signIn") }}</h1>
        <el-form-item>
          <el-input v-model="form.username" placeholder="Account ID"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="form.password"
            placeholder="Password"
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item class="sign-">
          <el-button
            type="primary"
            round
            class="sign-button"
            @click="signInSystem"
            >{{ $t("top.signIn") }}</el-button
          >
        </el-form-item>
        <el-form-item class="notice">
          <p>
            <el-link type="primary" :underline="false" href="#/signin">{{
              $t("top.forgotPwd")
            }}</el-link>
          </p>
          <p>
            {{ $t("top.noHaveAccount") }}
            <el-link type="primary" :underline="true" href="#/signin">{{
              $t("top.signUp")
            }}</el-link>
          </p>
        </el-form-item>
      </el-form></el-card
    >

    <el-dialog
      class="attention"
      :title="$t('top.attention')"
      :visible.sync="dialog.visible"
      width="400px"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
    >
      <p class="Warning">
        <span style="color:#000;font-weight:700">{{
          $t("top.lastSignInIP")
        }}</span
        >{{ dialog.ip }}
      </p>
      <p class="Warning">
        <span style="color:#000;font-weight:700">{{
          $t("top.lastSignInTime")
        }}</span
        >{{ dialog.time }}
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="redirectToHome">{{
          $t("alertComponent.confirm")
        }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import router from "@/router/index";

export default {
  data: function() {
    return {
      form: {
        username: "",
        password: "",
        captcha: ""
      },
      dialog: {
        ip: "",
        time: "",
        visible: false
      }
    };
  },
  computed: {
    ...mapGetters({
      menu: "lotteryMenuList"
    })
  },
  methods: {
    ...mapActions(["loginByUsername"]),
    signInSystem: function() {
      const { username, captcha, password, key = "" } = this.form;
      this.loginByUsername({
        username,
        verificationCode: captcha,
        password,
        key
      })
        .then(data => {
          data = data.data;
          this.dialog.ip = data.login_ip;
          this.dialog.time = data.signin_at;

          this.dialog.visible = true;
        })
        .catch(err => {
          this.$message({
            type: "error",
            message: err.message
          });
        });
    },
    redirectToHome: function() {
      this.dialog.visible = false;
      router.push({ name: "Home" });
    }
  },
  created() {}
};
</script>

<style lang="sass">
@import "../common/css/color.sass"

.attention
  .Warning
    color: $warning
  p
    line-height: 25px
    span
      padding-right: 10px

.signin-container
  background-image:url('./signIn/static/img/sign-in-bg.jpg')
  background-position: center
  background-size: cover
  padding-top: 50px
  padding-bottom: 30px
  .box-card
      width: 480px
      margin: 0 auto
      font-size: 20px
      font-weight: 700
      form
          width: 350px
          margin: 0 auto
          input
            border-width: 0 0 1px 0
            border-radius: 0
            margin-bottom: 20px
      h1
          font-size: 40px
          text-align: center
          display: block
          padding: 40px
      .sign-button
          width: 100%
          height: 44px
          margin: 0 auto
          font-size: 20px
      .notice
          text-align: center
          p
            line-height: 30px
</style>
